import * as type from '../types';

const defaultState = {
    data: [],
    status: false,
};

const userReducer = (state, action) => {

    switch (action.type) {
        case type.REGISTER_USER:
            return {
                data: [...state.data,action.payload],
            }

        case type.LOGIN_USER:
            let login = false;
            state.data.map((profile) => {
                if (profile.user === action.payload.user && profile.pass === action.payload.pass) {
                    login = true;
                }  
            })

            return {
                data: [...state.data],
                status: login,
            }  

        default:
            return defaultState;
    }
}


export default userReducer;