import React, { Component } from 'react';
import { Link, Navigate } from 'react-router-dom';
import { connect } from 'react-redux';
import { loginUser } from '../../actions';

export class Login extends Component {

    loginUser () {
        const userVal = this._userEle.value;
        const passVal = this._passEle.value;
        this._userEle.value = '';
        this._passEle.value = '';

        if (userVal!== '' && passVal!== '') {
            const data = {
                user: userVal,
                pass: passVal,
        }
        this.props.loginUser(data);
    }
        // this.props.userData.data.map((profile) => {
        //     if (profile.user === userVal && profile.pass === passVal) {
        //         this.status = true;
        //     }
            
        // })
        
        // this.status === false ? alert('Incorrect username or password') : console.log('nothing');
    }

    render() {
        if (this.props.userData.status) {
            return <Navigate to="/home"/>
        } else {
            return (
                <div style = {styles.main}>
                    <h1>Login here</h1>
                    <input type="text" placeholder = 'Username' ref = {(ele) => (this._userEle = ele)}/>
                    <input type="password" placeholder = 'Password' ref = {(ele) => (this._passEle = ele)}/>
                    <button onClick = {() => this.loginUser()}>Login</button>
                    <button><Link to='/register'>Register</Link></button>
                </div>
            )
        }
        
        
    }
}

const styles = {
    main: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        padding: '100px',
    },
};

const mapStateToProps = (state) => ({
    userData : state.user,
});

const mapDispatchToProps = {
    loginUser: loginUser,
};

export default connect(mapStateToProps,mapDispatchToProps)(Login);
